<template>
  <v-card raised elevation="12">
    <v-card-text>
      <h3 color="primary">Iniciar sesión</h3>
    </v-card-text>
    <v-card-text>
      <h1>+52 {{ this.phoneNumber }}</h1>
      <br />
      <v-btn text small color="primary" @click="goToLoginInit">¿Este no es tu número?</v-btn>
    </v-card-text>
    <v-card-actions>
      <v-col>
        <v-form ref="form" v-model="valid">
          <v-text-field
          v-on:keydown.enter.prevent="postVerificationCode"
            label="Código de verificación"
            :rules="codeRules"
            outlined
            required
            v-model="code"
          ></v-text-field>
          <v-btn
            type="button"
            :disabled="!valid"
            raised
            class=".btn"
            color="primary"
            @click="postVerificationCode"
            large
            block
          >Verificar</v-btn>
        </v-form>
      </v-col>
    </v-card-actions>
    <v-card-text>
      <v-btn :disabled="!resend" text small color="primary" @click="getVerificationCode">Reenviar</v-btn>
      <p>{{this.countDown}} segundos</p>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  mounted() {},
  computed: {
    ...mapState("login", ["phoneNumber"])
  },
  data: function() {
    return {
      valid: true,
      resend: false,
      code: "",
      countDown: 60,
      codeRules: [v => !!v || "El código de verificación es requerido"]
    };
  },
  created() {
    this.countDownTimer()
  },
  methods: {
    ...mapActions("login", [
      "postVerificationCodeAction",
      "getVerificationCodeAction"
    ]),
    postVerificationCode() {
      if (this.$refs.form.validate()) {
        this.postVerificationCodeAction(this.code);
      }
    },
    getVerificationCode() {
      this.resend = false
      this.countDown = 60
      this.countDownTimer()
      this.getVerificationCodeAction(this.phoneNumber);
    },
    goToLoginInit() {
      this.$router.push({ name: "phoneNumber" });
    },
    countDownTimer() {
      if (this.countDown > 0) {
        setTimeout(() => {
          this.countDown -= 1;
          this.countDownTimer();
        }, 1000);
      } else {
        this.resend = true
      }
    }
  }
};
</script>

<style lang="scss">
.color {
  color: var(--color-primary);
}
.btn {
  background: linear-gradient(to bottom left, var(--color-primary), white);
}

a {
  color: var(--color-primary);
  text-decoration: none;
  text-transform: uppercase;
}

a:hover {
  text-decoration: underline;
}
</style>